.stars {
  display: flex;
  list-style: none;
  padding: 0;

  &__star {
    background: url("/images/star.svg") no-repeat center;
    width: 16px;
    height: 16px;
  }

  &__star:not(:last-child) {
    margin-right: 4px;
  }

  @for $i from 1 through 5 {
    &--#{$i} .stars__star:nth-child(-n + #{$i}) {
      background-image: url("/images/star-active.svg");
    }
  }
}
